#skills{
    .skillsIconContainer{
        min-width: 100px;
        width: 150px;
        padding: 5px 10px;
        border-radius: 30px;
        border: 1px solid $color;
        color: $color;
        display: flex;
        justify-content: center;
        align-items: center;
        >svg{
            margin-right: 10px;
        }
    }
    .heading{
        margin-top: 4%;
        color: $color;
        >h2{
            >span{
                color: $colorchange;
            }
        }
    }
    .skillscontainer{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        &:hover{
            .container{
                border-color: $colorchange;
            }
        }
        .skill{
            border-radius: 30px;
            border: 1px solid $color;
            padding: 20px;
        }
        >p{
            margin-top: 10px;
            font-size: 20px;
            color: $color;
        }
    }
}