#testimonials{
    .testimonialIconContainer{
        min-width: 100px;
        width: 170px;
       padding: 5px 10px;
        border-radius: 30px;
        border: 1px solid $color;
        column-gap: 10px;
        color: $color;
        display: flex;
    }
    .heading{
        margin: 4% 0;
        color: $color;
        >h2{
            >span{
                color: $colorchange;
            }
        }
    }
    .slider{
        min-height: 40vh;
        border: 1px solid $color;
        border-radius: 30px;
        .items{
            color: $color;
            .design{
                display: flex;
                column-gap: 20px;
                align-items: center;
                .imageContainer{
                    width: 60px;
                    height: 60px;
                    >img{
                        border-radius: 50%;
                        width:100%;
                        height: 100%;
                        object-fit: cover;
                    }
            }
            }
        }
    }
}