#portfolio{
    
    .portfolioIconContainer{
        min-width: 100px;
        width: 150px;
       padding: 5px 10px;
        border-radius: 30px;
        border: 1px solid $color;
        color: $color;
        display: flex;
        >svg{
            margin-right: 10px;
        }
    }
    .heading{
        color: $color;
        margin-top: 4%;
        width: 100%;
        >h2{
            >span{
                color: $colorchange;
            }
        }
    }
    .container{
        &:hover{
            h4::after{
                width: 100%;
            }
        }
        .imageContainer{
            margin-top: 4%;

            >img{
                width: 100%;
                border-radius: 40px;
                object-fit: cover;
    
            }
        }
        >h4{
            color: $color;
            position: relative;
            text-align: center;
            &::after{
                position: absolute;
                content: "";
                width: 0%;
                height: 0.5vh;
                left: 0;
                bottom: -1px;
                background-color: $color;
                transition: 0.5s all;
            }
        }
    }
    .container2{
        position: relative;

        .innerContainer{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;            
            >h5{
                color: $color;
            }
            .imageContainer{
                position: relative;
                margin-top: 4%;
                width: 100%;
                height: 30vh;
                >img{
                    position: absolute;
                    width: 100%;
                    border-radius: 40px;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
        
    }
    
}