#pages{
    .sideProfile{
        background-color: transparent;
        border-radius: 30px;
        border:1px solid $color ;
        .header{
            padding: 0px 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: $color;
            >h4{
                color: $color;
            }
        }
        .profile{            
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            .imageContainer{
                margin: 20px 0;
                position: relative;
                width:65% ;
                height: 40vh;
                .innerImageContainer{
                position: relative;
                width: 100%;
                height: 40vh;
                >img{
                    position: absolute;
                    border-radius: 20px;
                    width: 100%;
                    object-fit: cover;
                    height: 100%;
                }
                } 
            }
            >h5{
                width: 65%;
                text-align: center;
                margin: 10px auto;
                color: $color;
            }
            >p{
                margin: 10px;
                color: $color;
            }
            .iconContainer{
                display: flex;
                justify-content: center;
                margin: 10px 0;
                >div{
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50%;
                    margin: 0 5px;
                    border: 2.1px solid $color;
                    transition: 0.5s all;
                    &:hover{
                        border-color:$colorchange ;
                        >svg{
                            color: $colorchange;
                        }
                    }
                    >svg{
                        transition: 0.5s all;
                        color: $color
                    }
                    
                }
            }

                >button{
                    margin-top: 3%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: transparent;
                    width: 70%;
                    letter-spacing: 0.1rem;
                    font-size: large;
                    padding: 15px 0;
                    transition:0.4s all ;
                    border: none;
                    font-weight: 300;
                    border-radius: 30px;
                    background-color: $colorchange;
                    &:hover{
                        background-color: transparent;
                        border: 2px solid $colorchange;
                    color: $colorchange;
                    >svg{
                        color: $colorchange;
                    }
                    }
                    >svg{
                        margin-right: 10px;
                    }
                }
            
        }
    }
    .sidebarIcons{
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 70vh;
        width: 100px;
        .humberIconContainer{
            padding: 8px;
            display: flex;
            justify-content: center;
            align-items: center;
            border:2px solid $color;
            border-radius: 50%;
            transition: 0.5s all;
            &:hover{
                color: $colorchange;
                border-color:$colorchange ;
                >svg{
                    color: $colorchange;
                }
            }
            >svg{
                transition: 0.5s all;
                color: $color;
            }
        }
        .iconsHandler{
            border-radius: 35px;
            padding: 10px;
            border: 1px solid $color;
            >a{
                transition: 0.5s all;
                color: $color;
                &:hover{
                    color: $colorchange;
                   
                }
            }
                >svg{
                    position: relative;
                    transition: 0.5s all;
                    color: $color;
                }
            // }
               
        }
    }
    
}

