#about{
    padding-left: 10px;
    .aboutIconContainer{
            min-width: 100px;
            width: 150px;
            padding: 5px 10px;
            border-radius: 30px;
            border: 1px solid $color;
            color: $color;
            display: flex;
    }
    .aboutPara{
        margin-top: 8%;
        >h2{
            color: $color;
            >span{
                color: $colorchange;
            }
        }
        >p{
            color: $color;
        }
    }
}