#resume{
    .resumeIconContainer{
        min-width: 100px;
            width: 150px;
            padding: 5px 10px;
            border-radius: 30px;
            border: 1px solid $color;
            color: $color;
            display: flex;
    }
    .aboutpara{
        margin: 4% 0;
        color: $color;
        >h2{
            >span{
                color: $colorchange;
            }
        }
    }
    .expirence{
        color: $color;
        &:hover{
            .innerExpirence{
                .curcleContainer{
                    .curcle{
                        background-color: $colorchange;
                    }
                }
                .top{
                    color: $colorchange;
                }
            }
            
            .innerExpirence{
                border-color: $colorchange;
            }
            
        }
        .innerExpirence{
            padding-top: 5%;
            border-left: 1px solid $color;
            transition:  0.5s all;

            .curcleContainer{
                position: relative;
                left: -8px;
                .curcle{
                    border-radius: 50%;
                    width: 15px;
                    height: 15px;
                    background-color: $color;
                    transition:  0.5s all;
                }
            }
            .details{
                >span{
                    font-size: 20px;
                }
                .top{
                    transition: 0.5s all;
                }
            }
        }
        
    }
}