#services{
    padding-left: 10px;
    .servicesIconContainer{
        min-width: 100px;
        width: 150px;
        padding: 5px 10px;
        border-radius: 30px;
        border: 1px solid $color;
        color: $color;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .heading{
        margin-top: 5%;
        >h2{
            color: $color;
            >span{
                color: $colorchange;
            }
        }
    }
    .projects{
        margin-top: 30px;
        color: $color;
        border: 1px solid $color;
        border-radius: 20px;
        min-height: 30vh;
        padding: 20px 10px 20px 20px;
        transition: 0.5s all;
        &:hover{
            border-color: $colorchange;
            color: $colorchange;
            >a{
                color: $colorchange;
            }
            a::after{
                width: 130%;
            }
        }
        .project_header{
            margin: 10px 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            >svg{
                margin-right: 30px;
            }
        }
        >p{
            margin: 30px 0;
        }
        >a{
            position: relative;
            color: $color;
            text-decoration: none;
            &::after{
                position: absolute;
                content: "";
                width: 0%;
                height: 2px;
                left: 0;
                bottom: -2px;
                background-color: $colorchange;
            transition: 0.5s all;

            }
        }
    }
}