#contact{
    .contactIconContainer{
        margin-bottom: 10px;
        min-width: 100px;
        width: 150px;
       padding: 5px 10px;
        border-radius: 30px;
        border: 1px solid $color;
        column-gap: 10px;
        color: $color;
        display: flex;
    }
    >div{
        .heading{
            margin: 1% 0;
            color: $color;
            >h2{
                >span{
                    color: $colorchange;
                }
            }
        }
        .mail{
            >a{
                text-decoration: none;
                color: $color;
            }
        }
        >form{
            margin-top: 20px;
            .fields{
                >div{
                    >label{
                        color: $color;
                    }
                    >div{
                        >input{
                            border: none;
                            outline: none;
                            background-color: transparent;
                            padding: 10px 0;
                            border-bottom: 1px solid $color;
                            color: $color;
                        }
                    }
                }
                .buttonContainer{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    >button{
                        padding: 10px 0px;
                        width: 300px;
                        border-radius: 30px;
                        color: $color;
                        background-color: $colorchange;
                        transition: 0.5s all;
                        border: none;
                        &:hover{
                            color: $colorchange;
                            border: 1px solid $colorchange;
                            background-color: transparent;
                        }
                    }
                }
               
            }
        }
    }
}

@media screen and(min-width:900px) {
    input{
        font-size: 20px;
    }
}
@media screen and(min-width:400px) {
    input{
        font-size: 18px;
    }
}